import { Directive, ElementRef, EventEmitter, Output, Renderer2 } from '@angular/core';

@Directive({
  selector: '[appSwipe]'
})
export class SwipeDirective {
  /** x position at touchstart */
  xDown = null;
  /** y position at touchstart */
  yDown = null;
  /** Timestamp at touchstart */
  time = 0;

  touchPos;
  startDifference;
  isScaler = false;


  @Output() swipeLeft: EventEmitter<any> = new EventEmitter();
  @Output() swipeRight: EventEmitter<any> = new EventEmitter();
  @Output() swipeUp: EventEmitter<any> = new EventEmitter();
  @Output() swipeDown: EventEmitter<any> = new EventEmitter();
  @Output() scrollUp: EventEmitter<any> = new EventEmitter();
  @Output() scrollDown: EventEmitter<any> = new EventEmitter();
  @Output() pinchIn: EventEmitter<any> = new EventEmitter();
  @Output() pinchOut: EventEmitter<any> = new EventEmitter();
  @Output() pinchRelease: EventEmitter<any> = new EventEmitter();

  constructor(private renderer: Renderer2, private elRef: ElementRef) {
  }

  ngAfterViewInit() {
    /** Listen for touchstart event on element directive is attached to */
    this.renderer.listen(this.elRef.nativeElement, 'touchstart', (event: TouchEvent) => {
      this.touchPos = event.changedTouches[0].clientY
      this.handleTouchStart(event);

      if (event.touches.length == 2) {
        this.startDifference = Math.hypot(
          event.touches[0].pageX - event.touches[1].pageX,
          event.touches[0].pageY - event.touches[1].pageY
        );
        if (this.startDifference) {
          this.pinchStart()
        }
      }
    });

    /** Listen for touchend event on element directive is attached to */
    this.renderer.listen(this.elRef.nativeElement, 'touchend', (event: TouchEvent) => {
      this.handleTouchMove(event);
      if (this.isScaler) {
        setTimeout(() => {
          this.pinchEnd();
        }, 500);
      }
    });
    var i ;
    this.renderer.listen(this.elRef.nativeElement, 'touchmove', (event: TouchEvent) => {
      const touch = event.touches[0] || event.changedTouches[0];

      const xUp = touch.pageX;
      const yUp = touch.pageY;
  
      const xDiff = this.xDown - xUp;
      const yDiff = this.yDown - yUp;

      // console.log('x Differnece',Math.abs(xDiff))
      // console.log('y Difference' , Math.abs(yDiff))

      const timeDiff = event.timeStamp - this.time;

console.log(this.isScaler)
      if(!this.isScaler) {
        if (timeDiff > 120)  {
          let newTouchPos = event.changedTouches[0].clientY;
          if (this.touchPos < newTouchPos) {
            this.touchPos = newTouchPos;
            this.scrollUp.emit('up')
          } 
          if (this.touchPos > newTouchPos) {
            this.touchPos = newTouchPos;
            this.scrollDown.emit('down')
          }
        }
      }

     



      

       
     


    });


  }



  pinchEnd() {
    setTimeout(() => {
      this.isScaler = false;
      this.pinchRelease.emit();
      }, 800);
  }


  pinchStart() {
    this.isScaler = true;
    if(this.isScaler){
      this.renderer.listen(this.elRef.nativeElement, 'touchmove', (event: TouchEvent) => {
        var dist = Math.hypot(
          event.touches[0].pageX - event.touches[1].pageX,
          event.touches[0].pageY - event.touches[1].pageY);
        if (this.startDifference < dist) {
          this.startDifference = dist;
          let fontSize = this.startDifference / this.startDifference;
          this.pinchIn.emit(fontSize);
        } else if (this.startDifference > dist) {
          this.startDifference = dist;
          let fontSize = this.startDifference / this.startDifference;
          this.pinchOut.emit(fontSize);
        }
      });
    }
   

  }

  private handleTouchStart(event: TouchEvent) {
    this.xDown = event.touches[0].pageX;
    this.yDown = event.touches[0].pageY;
    this.time = event.timeStamp;
  }


  handleTouchMove(event: TouchEvent) {
    if (!this.xDown || !this.yDown) {
      return;
    }

    const touch = event.touches[0] || event.changedTouches[0];

    const xUp = touch.pageX;
    const yUp = touch.pageY;

    const xDiff = this.xDown - xUp;
    const yDiff = this.yDown - yUp;
    const timeDiff = event.timeStamp - this.time;

    // simulate a swipe -> less than 500 ms and more than 60 px
    if (timeDiff < 500) {
      // touch movement lasted less than 500 ms
      if (Math.abs(xDiff) > 60 && Math.abs(yDiff) < 100) {
        // delta x is at least 60 pixels
        if (xDiff > 0) {
          this.swipeRight.emit(event);
        } else {
          this.swipeLeft.emit(event);
        }
      }

      if (Math.abs(yDiff) > 60) {
        // delta y is at least 60 pixels
        if (yDiff > 0) {
          this.swipeDown.emit(event);
        } else {
          this.swipeUp.emit(event);
        }
      }
    }

    // Reset values.
    this.xDown = null;
    this.yDown = null;
  }


}
