    zoomingIn(event) {
        this.event_provider.hideBibleHeaderFooter(false);
        if(this.fontSize < 22) {
            this.fontSize = this.fontSize + 0.5;
            this.storage.set('BibleFontSize' , this.fontSize);
            this.zoomed = true;
        }
    }
    zoomingOut(event) {
        this.event_provider.hideBibleHeaderFooter(false);
        if(this.fontSize > 15) {
            this.fontSize = this.fontSize - 0.5;
            this.storage.set('BibleFontSize' , this.fontSize);
            this.zoomed = false;
        }
    }
    pinchEnd() {
        //console.log("called")
        this.event_provider.hideBibleHeaderFooter(false);
    }
    doubleTap() {
        if(this.zoomed) {
            this.fontSize = 14;
            this.zoomed = false;
        } else if (!this.zoomed) {
            this.fontSize = 22;
            this.zoomed = true;
        }
    }
