    setBiblePageNo(type, book_order) {
        var obj = this;
            this._bibleService.getCMPbibleBooks(this.dam_id).subscribe(async (res: any) => {
                if (res) {
                    if (res && res.length > 0) {
                        let book = res.find(x => parseInt(x.bookOrder) === parseInt(book_order));
                        if (book) {
                            let chapter = {
                                id: (type === 'prev' ? parseInt(book.number_of_chapters) : 1),
                                selected: true
                            };
                            // item.chapters.split(',');
                       
                            let data: NavigationExtras = {
                                queryParams: {
                                    chapter: (type === 'languageChange') ? JSON.stringify(this.chapter) : JSON.stringify(chapter),
                                    book: JSON.stringify(book),
                                    dam_id: this.dam_id,
                                    page: this.dam_id[6],
                                    allChapters: obj.book.chapters,
                                    from: 'header',
                                }
                            };
                            console.log(data , "dataaaaaa")
                            this.router.navigate(['/bible-chapter'], data);
                            
                            // this.ngOnInit();
                        }
                    } else {
                        this.router.navigate(['/bible/new-testament']);
                    }
                } else if (!res) {
                    this.toast.presentToastWithOptions(res.message, (this.alertMessage ? this.alertMessage.SomeThingWrong : 'Something Went Wrong'), 'danger');
                }
            });

           
    
       
    }


    setCMPBiblePageNo(type, book_order) {
        let obj = this;
        let lang_code = this.langCode;
        this._bibleService.getCMPbibleBooks(this.dam_id).subscribe(async (res: any) => {
            // this.loader.stopLoading();
            if (res) {
                if (res && res.length > 0) {
                    let data: any = [];
                    res.forEach(function (x) {
                        let chapter = '';
                            for (let i = 1; i <= x.number_of_chapters ; i++) {
                                chapter += i + ",";
                                    chapter.slice(0, -1);
                                
                              }
                        x.expanded = false;
                        x.book_name = x.name;
                            x.book_id = x.id;
                            x.book_order = x.bookOrder;
                            x.chapters =  chapter,
                            x.lang_code = lang_code
                            console.log(obj.selected_page)
                            if(x.testament == obj.selected_page) {
                                data.push(x);
                            }
                    });
                    let book = data.find(x => parseInt(x.book_order) === parseInt(book_order));
                    if (book) {
                        let chapter = {
                            id: (type === 'prev' ? parseInt(book.number_of_chapters) : 1),
                            selected: true
                        };
                        let data: NavigationExtras = {
                            queryParams: {
                                chapter: (type === 'languageChange') ? JSON.stringify(this.chapter) : JSON.stringify(chapter),
                                book: JSON.stringify(book),
                                dam_id: this.dam_id,
                                page: obj.selected_page,
                                allChapters: this.book.chapters,
                                from: 'header',
                                audio_id: this.bible_chapter_audio_id
                            }
                        };
                        this.router.navigate(['/bible-chapter'], data);
                        this.ngOnInit();
                    }
                } else {
                    this.router.navigate(['/bible/new-testament']);
                }
            } else if (!res) {
                this.toast.presentToastWithOptions(res.message, (this.alertMessage ? this.alertMessage.SomeThingWrong : 'Something Went Wrong'), 'danger');
            }
        });

   
}


