      playbible(isplay, audioUrl2, from){
        if(from === 'bible'){
            if(this.audioService.audioUrl == audioUrl2){
                this.audioService.togglePlayer(isplay);
              }else{
                this.audioService.clear();
                setTimeout(() => {
                  this.audioService.clear();
                  this.audioService.image = this.ttb_bible_badge;
                  this.audioService.audioUrl = audioUrl2;
                  this.chapterAudio.image = this.ttb_bible_badge;
                  this.chapterAudio.title = this.book.book_name_eng+' '+this.chapterAudio.number 
                  this.common.audioData = this.chapterAudio;
                  this.audioService.title = this.book.book_name_eng+' '+this.chapterAudio.number;
                  this.common.method = "bible" ;
                  this.audioService.fromPage = 'bible';
                  this.audioService.liveStreaming = false;
                  this.audioService.title  = this.book.book_name_eng+' '+this.chapterAudio.number;
                  this.audioService.bible_playing[audioUrl2] = true;
      
              //   this.audioService.fromPage = "study";
                 this.audioService.bible_index = audioUrl2;
                this.audioService.startAudio(audioUrl2);
                },500);
            
            }
        }else if(from === 'study'){
            if(this.audioService.audioUrl == audioUrl2){
                this.audioService.togglePlayer(isplay);
              }else{
                this.audioService.clear();
                setTimeout(() => {
                  this.audioService.clear();
                  this.audioService.image = this.ttb_bible_badge;
                  this.audioService.audioUrl = audioUrl2;
                //   this.chapterAudio.image = this.ttb_bible_badge;
                //   this.chapterAudio.title =this.ttb_teaching_data.name;
                 // this.common.audioData = this.chapterAudio;
                  this.audioService.title = this.ttb_teaching_data.name;
                  this.common.method = "bible" ;
                  this.audioService.fromPage = 'bible';
                  this.audioService.liveStreaming = false;
                  this.audioService.bible_playing[audioUrl2] = true;
                 this.audioService.bible_index = audioUrl2;
                this.audioService.startAudio(audioUrl2);
                },500);
            
            }

        }

    }



